



































































































































































































import { Vue, Component } from "vue-property-decorator";

@Component
export default class PatientManagementForm extends Vue {
  patientID: string = "";
  firstName: string = "";
  lastName: string = "";
  genderText: string = "";
  maritalText: string = "";
  telephone: string = "";
  address: string = "";
  birthDate: string = "";
  action: string = "add";
  gender: Object = ["男", "女", "其他", "不願提供"];
  maritalStatus: Object = ["未婚", "已婚", "離婚", "喪偶", "不願提供"];
  data() {
    return {
      picker: new Date().toISOString().substr(0, 10),
      treatDate: new Date().toISOString().substr(0, 10),
      birthDate: "",
      treatMenu: false,
      birthMenu: false
    };
  }
  clear() {
    this.patientID = "";
    this.firstName = "";
    this.lastName = "";
    this.genderText = "";
    this.maritalText = "";
    this.telephone = "";
    this.address = "";
    this.birthDate = "";
  }
}
